import React, { useEffect, useState } from 'react';
import './style.scss';

import signupFn from '../../controller/signupFn';
import selectGenderFn from '../../controller/selectGenderFn';
import onChange from '../../controller/onChange';

function Signup(props) {
    const [genderSwitch, setGenderSwitch] = useState(false);
    const [joinAccount, setJoinAccount] = useState({ 
        name: '', 
        id: '', 
        password: '', 
        psCheck: '', 
        gender: '', 
        imagePath: '' 
    });

    useEffect(()=>{
        const arr = { ...joinAccount }
        arr.gender = 'male';
        setJoinAccount(arr);
    }, []);
    return <div className="signup">
        <h1></h1>
        <div className="password-comment">
        <p> 6 ~ 20 ,</p>
        <p>, ,   2   .</p>
        </div>
        <section id="signup-form">
            <input 
            onChange={e => onChange(e, joinAccount, setJoinAccount)} name="name" type="text" id="name" placeholder="" required
            />
            <input 
            onChange={e => onChange(e, joinAccount, setJoinAccount)} name="id" type="email" id="email" placeholder="" required
            />
            <input 
            onChange={e => onChange(e, joinAccount, setJoinAccount)} name="password" type="password" id="password" placeholder="" required
            />
            <input 
            onChange={e => onChange(e, joinAccount, setJoinAccount)} type="password" name="psCheck" placeholder=" " required
            />
            <div className="gender">
                <button
                    style={{ background: genderSwitch ? "white" : "#2647ff",
                                color: genderSwitch ? "#808080" : "white" }}
                    className="male-btn"
                    type="button"
                    onClick={()=>{
                        setGenderSwitch(!genderSwitch);
                       selectGenderFn(genderSwitch, joinAccount, setJoinAccount);
                   }}></button>
                <button 
                    style={{ background: genderSwitch ? "#2647ff" : "white",
                                color: genderSwitch ? "white" : "#808080" }}
                    className="female-btn"
                    type="button"
                    onClick={()=>{
                        setGenderSwitch(!genderSwitch);
                        selectGenderFn(genderSwitch, joinAccount, setJoinAccount);
                    }}
                ></button>
            </div>
            <button onClick={()=>{
                signupFn(joinAccount, setJoinAccount, props.history);
            }} 
            className="signup-btn" 
            type="submit"></button>
            <button className="cancel-btn" onClick={()=>{
                props.history.goBack();
            }} type="button" ></button>
        </section>
    </div>
}

export default React.memo(Signup);