const chatAlarm = function (messageData, dispatch) {
    if (messageData) {
        dispatch({ type: "SWITCH_ALARM" });
        // console.log(" ON");
        const alarmTimer = setTimeout(() => {
            dispatch({ type: "SWITCH_ALARM" });
            dispatch({ type: "ALARM_MESSAGE", payload: null });
            // console.log(" OFF");
        }, 2000);
    } else {
        return console.log(" ");
    }
};

export default chatAlarm;
